import React, { Suspense, lazy } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Box, CircularProgress } from '@mui/material';

// Layout Components
import Header from './components/layout/Header';
import Footer from './components/layout/Footer';

// Lazy load page components for code splitting
const HomePage = lazy(() => import('./pages/HomePage'));
const AnalysisCentury21Page = lazy(() => import('./pages/AnalysisCentury21Page'));
const AnalysisAllNeighborhoodsPage = lazy(() => import('./pages/AnalysisAllNeighborhoodsPage'));
const DataMethodologyPage = lazy(() => import('./pages/DataMethodologyPage'));
const DownloadsPage = lazy(() => import('./pages/DownloadsPage'));
const KaggleInfoPage = lazy(() => import('./pages/KaggleInfoPage'));
const AboutPage = lazy(() => import('./pages/AboutPage'));

// Loading fallback component
const LoadingFallback = () => (
  <Box 
    display="flex" 
    justifyContent="center" 
    alignItems="center" 
    minHeight="70vh"
  >
    <CircularProgress />
  </Box>
);

// Define theme
const theme = createTheme({
  palette: {
    primary: {
      main: '#2c7fb8',
    },
    secondary: {
      main: '#5ab4ac',
    },
    background: {
      default: '#ffffff',
    },
  },
  typography: {
    fontFamily: [
      'Roboto',
      'Arial',
      'sans-serif',
    ].join(','),
    h1: {
      fontWeight: 500,
    },
    h2: {
      fontWeight: 500,
    },
    h3: {
      fontWeight: 500,
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Header />
        <main>
          <Suspense fallback={<LoadingFallback />}>
            <Routes>
              <Route path="/" element={<HomePage />} />
              <Route path="/analysis-century21" element={<AnalysisCentury21Page />} />
              <Route path="/analysis-all-neighborhoods" element={<AnalysisAllNeighborhoodsPage />} />
              <Route path="/data-methodology" element={<DataMethodologyPage />} />
              <Route path="/downloads" element={<DownloadsPage />} />
              <Route path="/kaggle-info" element={<KaggleInfoPage />} />
              <Route path="/about" element={<AboutPage />} />
            </Routes>
          </Suspense>
        </main>
        <Footer />
      </Router>
    </ThemeProvider>
  );
}

export default App;